@import "mixin";

@media all  and (min-width: 1400px) {
	#make {
		.make-list {
			.make-item {
				font-size: 36px; } } }
	#call {
		.contact {
			.cont-list {
				.cont-link {
					font-size: 26px; } } } }
	footer {
		div {
			font-size: 18px;
			span {
				font-size: 18px; } } } }

@media all  and (max-width: 1200px) {
	#make {
		.make-list {
			padding-left: 6%; } }
	#skills {
		.skill-text {
			font-size: 26px; } }
	#first {
		.motto {
			font-size: 36px; } } }


@media all  and (max-width: 1060px) {
	#works {
		.carousel {
			height: 240px !important;
			.carousel-cell {
				.more {
					height: 30px;
					font-size: 18px; } } } }
	#make {
		.make-list {
			// padding-left: 6%
			width: 74%;
			@include clmn-gap(0em);

			.make-item {
				font-size: 22px; } } }
	#skills {
		.skills-bar {
			width: 33%; } } }

@media all  and (max-width: 900px) {
	#call {
		.contact {
			.cont-list {
				// width: 80%
				.cont-link {
					// width: 40%
 } } } }					// margin: 0
	// #make
	// 	.make-list
	// 		padding-left: 7%
	// 		.make-item
	// 			margin-left: 10%
	.popup-more {
		width: 90%;
		min-height: 400px;
		flex-direction: column;
		align-items: center;
		justify-content: space-between;
		overflow: auto;
		.popup-img,
		.popup-text {
			width: 90%;
			margin: 5% auto 0;
			top: 0;
			@include transform(none); }
		.open {
			height: 30px;
			position: relative;
			margin-top: 15px; } } }
