@media all  and (max-width: 896px) {
	#call {
		.contact {
			.cont-list {
				justify-content: space-between;
				.cont-link {} } } }
	#skills {
		.skill-text {
			font-size: 22px; } } }

@media all  and (max-width: 795px) {
	#up {
		@include scale(0.8); }
	#make {
		.make-list {
			@include clmn-count(1);
			text-align: center;
			padding-left: 0;
			width: 80%;
			.make-item {
				margin-left: 0; } } }

	#call {
		.contact {
			.cont-list {
				width: 95%;
				padding-left: 30%;
				.cont-link {
					width: 65%;
 } } } }					// text-align: left
	.carousel--show-several .carousel-cell {
		width: 45%; } }

@media all  and (max-width: 670px) {
	#first:before {
		@include box-shadow(inset 0px 0px 350px 50px #000); }
	#works:before {
		@include box-shadow(inset 0px 0px 350px 100px #000); }
	.nav-menu:before {
		left: 0;
		@include transform(none);
		width: 100%;
		height: 120%;
		border-radius: 0 0 5px 0; }

	.nav-menu {
		flex-direction: column;

		width: 30%;
		min-width: 160px;
		max-height: 200px;
		height: 200px;
		.nav-a {
			width: 100%; }
		.close {
			top: 20px;
			left: 0;
			// width: 20px
 }			// height: 20px
		.close:before, .close:after {
			top: -5px;
			// left: 10px
 } }			// height: 21px


	.popup-about {
		padding-top: 0;
		.about-name {
			display: block;
			text-align: center; }
		div {
			span {
				font-size: 16px; } } }

	// #make
	// 	.make-list
	// 		width: 70%
	// 		padding-left: 10%

	#call {
		.contact {
			.cont-list {
				width: 100%;
				// padding-left: 7%
				.cont-link {
					font-size: 20px; } } } } }

@media all  and (max-width: 620px) {
	#first {
		.motto {
			font-size: 30px; } }
	.popup-about {
		.about-name {
			font-size: 30px; } }
	#call {
		.contact {
			.cont-list {
				width: 90%;
				text-align: center;
				padding-left: 0;
				.cont-link {
					width: 100%; } } }
		.row {
			.well {
				.form {
					.row {
						.form-group {
							width: 100%;
							padding: 0; } } } } } }
	footer {
		div {
			font-size: 12px;
			span {
				font-size: 10px; } } } }



@media all and (max-width: 426px) {

	.menu-button:before {
		animation: none;
		border: none;
		background: $oceanblue;
		opacity: 0.5; }
	.menu-button {
		// left: 0
		width: 35px;
		height: 35px;
		.menu-capt {
			display: none; }
		.ninja-button {
			display: block; } }

	.nav-menu:before {
		background: $oceanblue;
		border-right: 0 0 15px 0; }
	.close {
		opacity: 1; }
	.close:before, .close:after {
		background: $white; }

	.site-h {
		padding: 4.5% 0; }

	.carousel--show-several .carousel-cell {
		width: 70%; }
	#works {
		.carousel {
			.carousel-cell {
				.more {
					font-size: 14px;
					// right: 14%
					// right: 0
					// bottom: 0
					// bottom: -100%
					// transform: translateY(-100%)
					// width: 100%
					// margin: auto
					// position: relative
 } } } }					// text-align: center

	#skills {
		.skills-bar {
			.small {
				font-size: 60px; } }
		.skill-text {
			display: none; } }
	.popup-about {
		width: 80%;
		div {
			font-size: 18px;
			span {
				font-size: 14px; } }
		.about-name {
			font-size: 30px; }
		ul {
			padding-left: 10px; } }

	#call {
		.contact {
			.cont-list {
				.cont-link {
					font-family: FuturaPTL;
					font-size: 20px; } } } } }
