@mixin clmn-count($number) {
	column-count: $number;
	-moz-column-count: $number;
	-webkit-column-count: $number; }

@mixin clmn-gap($gap) {
	-webkit-column-gap: $gap;
	-moz-column-gap: $gap;
	column-gap: $gap; }

@mixin animation($animation) {
	-webkit-animation: $animation {
  animation: $animation; } }



